import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ToastController, ViewController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';

@IonicPage()
@Component({
  selector: 'page-notas',
  templateUrl: 'notas.html',
})
export class NotasPage {

  private id: string = this.navParams.get('id')
  private type: string = this.navParams.get('type')
  public notas: any[] = []

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private modal: ModalController,
    private back: BackProvider,
    private toast: ToastController,
    public viewCtrl: ViewController
  ) {
  }

  ionViewDidLoad() {
    this.back.getNotas(this.id, this.type).subscribe(data => {
      this.notas = data
    }, err => {
      this.toast.create({message: 'No se pudieron cargar tus notas...', duration: 2000}).present()
      if (this.navCtrl.canGoBack()) this.navCtrl.pop()
    })
  }

  openAdd(id: string, type: string) {

    const m = this.modal.create('NotasAddPage', {id, type})
    m.present()

    m.onDidDismiss(data => {
      if(!data) return
      this.ionViewDidLoad()
    })
    
  }

}



